import VideoIcon from 'vue-material-design-icons/Video.vue';
import PlayCircleIcon from 'vue-material-design-icons/PlayCircle.vue';
import { decode } from 'blurhash';
import { generateUrl } from '@nextcloud/router';
import { NcCheckboxRadioSwitch } from '@nextcloud/vue';
import FavoriteIcon from './FavoriteIcon.vue';
import { isCachedPreview } from '../services/PreviewService.js';
export default {
  name: 'File',
  components: {
    FavoriteIcon,
    NcCheckboxRadioSwitch,
    VideoIcon,
    PlayCircleIcon
  },
  inheritAttrs: false,
  props: {
    file: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    },
    allowSelection: {
      type: Boolean,
      default: true
    },
    distance: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      initialized: false,
      loadedSmall: false,
      errorSmall: false,
      loadedLarge: false,
      errorLarge: false
    };
  },
  computed: {
    /** @return {string} */
    ariaLabel() {
      if (this.file.favorite) {
        return t('photos', 'Favorite image, open the full size "{name}" image', {
          name: this.file.basename
        });
      }
      return t('photos', 'Open the full size "{name}" image', {
        name: this.file.basename
      });
    },
    /** @return {boolean} */
    isImage() {
      return this.file.mime.startsWith('image');
    },
    /** @return {string} */
    decodedEtag() {
      return this.file.etag.replace('&quot;', '').replace('&quot;', '');
    },
    /** @return {string} */
    srcLarge() {
      return this.getItemURL(512);
    },
    /** @return {string} */
    srcSmall() {
      return this.getItemURL(64);
    },
    /** @return {boolean} */
    isVisible() {
      return this.distance === 0;
    },
    hasBlurhash() {
      return this.file.metadataBlurhash !== undefined;
    }
  },
  watch: {
    async file() {
      this.initialized = false;
      this.loadedSmall = false;
      this.errorSmall = false;
      this.loadedLarge = false;
      this.errorLarge = false;
      await this.init();
    }
  },
  async mounted() {
    await this.init();
  },
  beforeDestroy() {
    // cancel any pending load
    if (this.$refs.imgSmall !== undefined) {
      this.$refs.imgSmall.src = '';
    }
    if (this.$refs.srcLarge !== undefined) {
      this.$refs.srcLarge.src = '';
    }
  },
  methods: {
    async init() {
      [this.loadedSmall, this.loadedLarge] = await Promise.all([await isCachedPreview(this.srcSmall), await isCachedPreview(this.srcLarge)]);
      this.initialized = true;
      await this.$nextTick(); // Wait for next tick to have the canvas in the DOM

      this.drawBlurhash();
    },
    emitClick() {
      this.$emit('click', this.file.fileid);
    },
    onLoadSmall() {
      this.loadedSmall = true;
    },
    onLoadLarge() {
      this.loadedLarge = true;
    },
    onErrorSmall() {
      this.errorSmall = true;
    },
    onErrorLarge() {
      this.errorLarge = true;
    },
    onToggle(value) {
      this.$emit('select-toggled', {
        id: this.file.fileid,
        value
      });
    },
    getItemURL(size) {
      const token = this.$route?.params.token;
      if (token) {
        return generateUrl(`/apps/photos/api/v1/publicPreview/${this.file.fileid}?etag=${this.decodedEtag}&x=${size}&y=${size}&token=${token}`);
      } else {
        return generateUrl(`/apps/photos/api/v1/preview/${this.file.fileid}?etag=${this.decodedEtag}&x=${size}&y=${size}`);
      }
    },
    drawBlurhash() {
      if (!this.hasBlurhash || !this.$refs.canvas) {
        return;
      }
      const width = this.$refs.canvas.width;
      const height = this.$refs.canvas.height;
      const pixels = decode(this.file.metadataBlurhash, width, height);
      const ctx = this.$refs.canvas.getContext('2d');
      const imageData = ctx.createImageData(width, height);
      imageData.data.set(pixels);
      ctx.putImageData(imageData, 0, 0);
    }
  }
};